<template>
  <!-- begin:: Page -->
  <div>
    <div style="margin: 16px;">
      <van-button round block type="info" @click="getLocation()"
        >获取定位</van-button
      >
    </div>
    <van-cell-group>
      <van-cell title="经度">
        <template #default>
          <span>{{ latitude }}</span>
        </template>
      </van-cell>
      <van-cell title="纬度">
        <template #default>
          <span>{{ longitude }}</span>
        </template>
      </van-cell>
      <van-cell v-if="showErr" title="错误">
        <template #default>
          <span>{{ err }}</span>
        </template>
      </van-cell>
      <div style="margin: 16px;">
        <van-button round block type="info" @click="getLocation()"
          >扫码签到</van-button
        >
      </div>
    </van-cell-group>
  </div>
</template>

<script>
import jsQR from "jsqr";
import Quagga from "quagga";
export default {
  name: "Home",
  components: {},
  data() {
    return {
      showErr: false,
      latitude: "",
      longitude: "",
      err: ""
    };
  },
  // mounted() {
  //   //  if(this.$cookies.get("usid") === null) {
  //   //    window.location.href = '/';
  //   //  }
  // },

  methods: {
    getLocation() {
      // HTML5方法
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          this.showPosition,
          this.showError
        );
      } else {
        this.err = "Geolocation is not supported by this browser.";
        console.log("此浏览此不支持定位");
        this.showErr = true;
      }
    },
    showPosition(position) {
      this.latitude = position.coords.latitude;
      this.longitude = position.coords.longitude;
      this.showErr = false;
      // this.location = true;
    },
    showError(error) {
      switch (error.code) {
        case error.PERMISSION_DENIED:
          console.log("User denied the request for Geolocation.");
          this.err = "定位权限获取失败";
          break;
        case error.POSITION_UNAVAILABLE:
          console.log("Location information is unavailable.");
          this.err = "定位信息获取失败";
          break;
        case error.TIMEOUT:
          console.log("The request to get user location timed out.");
          this.err = "获取定位超时";
          break;
        case error.UNKNOWN_ERROR:
          console.log("An unknown error occurred.");
          this.err = "发生未知错误";
          break;
      }
      this.showErr = true;
    }
  }
};
</script>



